@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-body {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
}
.site-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  margin-top: -2px;
}
.site-list__item {
  padding: 23px 0;
  border-bottom: 1px solid #E6E6E6;
  min-width: 320px;
  text-align: center;
  margin-left: -20px;
}
.site-list__item a {
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
}
.site-list__item:first-child {
  border-top: 1px solid #E6E6E6;
  margin-top: 17px;
}
@media (min-width: 768px) {
  .site-list {
    flex-direction: row;
    min-width: 440px;
  }
  .site-list__item {
    position: relative;
    border: none;
    min-width: max-content;
    margin: 0 0 0 35px;
    padding: 20px 0 7px 0;
  }
  .site-list__item--active::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    bottom: -2px;
    height: 2px;
    background-color: #68B738;
  }
  .site-list__item:first-child {
    margin-top: 0;
    border: none;
  }
}
@media (min-width: 1440px) {
  .site-list {
    margin-top: 0;
  }
  .site-list__index .site-list__item a {
    color: #FFFFFF;
    font-size: 20px;
  }
  .site-list__item a {
    font-size: 20px;
  }
  .site-list__item:first-child {
    margin-left: 0;
  }
  .site-list__index .site-list__item--active::after {
    background-color: #FFFFFF;
  }
}
.program {
  color: #FFFFFF;
}
.program__heading {
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  text-align: center;
  margin-top: 27px;
  margin-bottom: 23px;
  padding-top: 27px;
}
.program__paragraph {
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  text-align: center;
}
.program__wrapper {
  position: relative;
  width: 320px;
  min-height: 304px;
  z-index: -1;
  margin: 0 auto;
}
.program__wrapper::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 320px;
  height: 301px;
  background-color: #68B738;
  z-index: -1;
}
.program__wrapper::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 320px;
  height: 301px;
  background-image: url("../img/cat_program.jpg");
  z-index: -1;
  opacity: 0.2;
}
@media (min-resolution: 2dppx) {
  .program__wrapper::after {
    background-image: url("img/cat_program@2x.jpg");
  }
}
.program__image {
  margin-left: 20px;
  margin-top: 16px;
}
.program__link {
  display: block;
  width: 280px;
  text-align: center;
  font-size: 14px;
  line-height: 18px;
  color: #FFFFFF;
  background-color: #68B738;
  text-transform: uppercase;
  text-decoration: none;
  padding: 11px 0;
  margin-left: 20px;
}
@media (min-width: 768px) {
  .program {
    position: relative;
  }
  .program h2,
  .program p {
    color: #000000;
  }
  .program__heading {
    font-size: 60px;
    line-height: 60px;
    margin: 42px 10px 0 87px;
    width: 414px;
    text-align: left;
  }
  .program__paragraph {
    font-size: 20px;
    line-height: 20px;
    text-align: left;
    margin-top: 41px;
    margin-left: 89px;
  }
