    display: block;
    width: 1220px;
  }
  .program__wrapper::before {
    display: block;
    top: -194px;
    left: 610px;
    width: 720px;
    height: 694px;
  }
  .program__wrapper::after {
    display: block;
    top: -194px;
    left: 610px;
